import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

/** 비동기 처리 함수 구현 */
// 여러개의 Slice가 사용되는 경우 함수에 적용되는 별칭이 다른 파일과 중복되지 않도록 "파일명/함수별칭" 등으로 지정해야 한다.
export const getBlogList = createAsyncThunk("Blog/GET_LIST", async (payload, { rejectWithValue }) => {
    let result = null;

    // 절달되는 파라미터가 있을 경우에만 비동기 처리 수행
    if(payload){
      try {
        const apiUrl = 'https://dapi.kakao.com//v2/search/blog';
        result = await axios.get(apiUrl, {
          params: {
              query: payload, 
          },
          headers:{
              Authorization:'KakaoAK a4711b1716f82c5243d4ad07178e749b'
          }
        });
  
      } catch (err) {
        result = rejectWithValue(err.response);
      }
    }
    
    return result;
  }
);

/** Slice 정의 (Action함수 + Reducer의 개념) */
const blogSlice = createSlice({
    name: "blog",
    initialState: {
      /** 상태값 구조 정의 */
      rt: null,       // HTTP 상태 코드(200,404,500 등)
      rtmsg: null,    // 에러메시지
      item: [],       // Ajax 처리를 통해 수신된 데이터
      loading: false, // 로딩 여부
    },
    // 내부 action 및 동기 action (Ajax처리시에는 사용하지 않음)
    reducers: {},
    // 외부 action 및 비동기 action
    extraReducers: {
      [getBlogList.pending]: (state, { payload }) => {
        return { ...state, loading: true };
      },
      [getBlogList.fulfilled]: (state, { payload }) => {
          return {
              ...state,
              rt: payload.status,
              rtmsg: payload.statusText,
              item: payload.data,
              loading: false,
          };
      },
      /** Ajax 요청 실패 */
      [getBlogList.rejected]: (state, { payload }) => {
        return {
          ...state,
          rt: payload?.status ? payload.status :'500',
          rtmsg: payload?.statusText ? payload.statusText : 'Server Error',
          item: payload?.data,
          loading: false,
        };
      },
    },
  });
  
  // 리듀서 객체 내보내기
  export default blogSlice.reducer;
  